
html, body {
  font-family: 'Roboto', 'Helvetica', sans-serif;
  margin: 0;
  padding: 0;
}

.navbar-dg {
  background-color: transparent;
  border-color: transparent;
  height: 55px;
}

.navbar-dg-logo {
  padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
    padding-right: 10px;
}

.mar-no {
  margin-bottom: 0px;
}

.scrolloff {
      pointer-events: none;
  }

  .btn-radius-6 {
    border-radius: 6px;
  }

  .btn-white {
    color: white;
  }

  .btn-yellow {
    background: #F7CA18;
  }

  .btn-hover-yellow {
    background: rgba(255, 255, 255, 0.5);
    border-color: white;
  }

  .btn-hover-yellow:hover {
    background: transparent;
  }

  .btn-hover-green {
    background: transparent;
    border-color: white;
  }

  .btn-hover-green:hover {
    background: #1abc9c;
  }

  .p-color {
    color: #d2d2d2;
  }

  .dg-logo {
    padding-top: 4px;
    padding-bottom: 3px;
  }

  .dg-logo-position {
    top: 20px;
    left: 20px;
    position: absolute;
    width: 60px;
  }


  .dg-logo-navbar-position {
    position: absolute;
    left: 20px;
    width: 86px;
  }

  .navbar-brand > .dg-logo-navbar-position {
    display: none;
  }

  .scrolled {
    .dg-logo-position {
      display: none;
    }
    .dg-logo-navbar-position {
      display: block;
    }
 }

  .btn-text-bold {
    text-transform: none;
    font-weight: 600;
  }

  .navbar-default .navbar-nav > .active > a, .navbar-default .navbar-nav > .active > a:hover, .navbar-default .navbar-nav > .active > a:focus {
    color: white;
    background-color: transparent;
    border-bottom: 1px solid white;
    padding-bottom: 3px;
  }

  @media screen and (max-width: 480px) {

    .navbar-dg {
    background-color: #1abc9c;
    border-color: #1abc9c;
    height: 55px;
  }

        .navbar-default .navbar-nav > .active > a, .navbar-default .navbar-nav > .active > a:hover, .navbar-default .navbar-nav > .active > a:focus {
      color: white;
      background-color: #1abc9c;
      border-bottom: 1px solid white;
      padding-bottom: 3px;
    }

    .dg-logo-position {
      top: 5px;
      left: 20px;
      position: absolute;
      width: 40px;
    }

  .padding-top-2 {
    background-color: #1abc9c;
  }

  #bannertext h1 {
      position: relative;
      margin-bottom: 0;
      color: white;
      letter-spacing: 2px;
      font-weight: 900;
      font-size: 2.1em;
      line-height: 1em;
  }

  #bannertext {
      position: fixed;
      top: 26%;
      right: 0;
      left: 0;
      z-index: 2;
      margin: auto;
      max-width: 1200px;
      color: #fff;
      text-align: center;
  }

  #bannertext h3 {
      position: relative;
      color: white;
      font-size: 10px;
      font-weight: 300;
      letter-spacing: 2px;
  }

  #bannertext bold {
      position: relative;
      color: white;
      font-size: 14px;
      font-weight: 700;
      letter-spacing: 2px;
  }
    }

  .navbar-default .navbar-nav > li > a {
    color: white;
}

//Add the below transitions to allow a smooth color change similar to left
.navbar {
    -webkit-transition: all 0.6s ease-out;
    -moz-transition: all 0.6s ease-out;
    -o-transition: all 0.6s ease-out;
    -ms-transition: all 0.6s ease-out;
    transition: all 0.6s ease-out;
}

.navbar.scrolled {
    background: #1abc9c; //IE
    border: #1abc9c; //NON-IE
}

.padding-top-8 {
  padding-top: 8px !important;
}

.padding-top-2 {
  padding-top: 2px;
}
